import React from 'react';
import { Container, Tab, Tabs } from 'react-bootstrap';
import RdDeposites from './RdDeposites';
import RdPaid from './RdPaid';

function RdCollectionTabs(props) {
    return (
        <>
        <Container fluid>
        <h2 className="text-info text-center">RD applications</h2>
            <Tabs
                defaultActiveKey="home"
                transition={false}
                id="noanim-tab-example"
                className="mb-3">
                <Tab eventKey="home" title="Deposit">
                    <RdDeposites/>
                </Tab>
                <Tab eventKey="profile" title="Paid">
                    <RdPaid />
                </Tab>
            </Tabs>                    
            </Container>
        </>
    );
}

export default RdCollectionTabs;