import React, { useEffect, useState, useMemo, useRef } from 'react';
import { Table, Button, Container, Row, Col, Modal, Pagination, Form, Alert } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import RDApplications from '../../models/RDApplications';
import User from '../../models/user';
import Loader from '../layout/Loader';
import moment from 'moment';
import { useTable, useSortBy, usePagination, useFilters } from 'react-table'
import rDApplications from '../../models/RDApplications';

import { useHistory } from 'react-router-dom';
import { useSelector, useDispatch } from "react-redux";

function RdDeposites(props) {
    const auth = useSelector(state => state.auth);
    const amountInputRefArr = [];
    let history = useHistory();
    const [isShowLoader, setisShowLoader] = useState(false);
    const [entries, setEntries] = useState([]);
    const [rdAccounts, setRdAccounts] = useState([]);
    const [showDeleteModel, setShowDeleteModel] = useState(false)
    const [amount, setAmount] = useState("");
    const [showEntryModel, setShowEntryModel] = useState(false);
    const [selectedEntryUpdate,setSelectedEntryUpdate] = useState("");
    const [paidID, setPaidID] = useState({
        "account_number":"",
        "deposited_date":"",
        "deposited_amount":"",
        "is_deposited":1,
        "is_account_open_amount":0
    })

    useEffect(() => {
        getMemberGroup(0);
    }, [])

    const data = useMemo(
        () =>
            rdAccounts.map((record, index) => {
                return {
                    col3: record.agent_name,
                    col1: record.account_number,
                    col2: record.account_holder_name,
                    col4: record.address,
                    col5: record.phone,
                    col6: record.totalDeposited,
                    col9: record,
                }
            }
            )
        ,
        [rdAccounts]
    )

    const columns = useMemo(
        () => [
            {
                Header: 'Agent name',
                accessor: 'col3', // accessor is the "key" in the data
                allowFilter: true,

            },
            {
                Header: 'Account number',
                accessor: 'col1', // accessor is the "key" in the data
                allowFilter: true,
            },
            {
                Header: 'Account holder',
                accessor: 'col2',
                allowFilter: true
            },
            {
                Header: 'Address',
                accessor: 'col4',
                allowFilter: true
            },
            {
                Header: 'Phone',
                accessor: 'col5',
                allowFilter: true
            },
            {
                Header: 'Account Balance',
                accessor: 'col6',
                allowFilter: true
            },
            {
                Header: 'Action',
                accessor: 'col9',
                allowFilter: false,
                Cell: emi => {
                    return (
                    <>
                    <Button className="mb-2" size={"sm"} variant="info" onClick={() => entriesHandle(emi.value.account_number)}>Get Entries</Button>
                    
                    <Button size={"sm"} variant="success" onClick={() => paidHandle(emi.value.account_number)}>Deposit</Button>
                    </>
                    )}

            }
        ],
        []
    )
    const entriesHandle = (account_number)=>{
        setEntries([]);
        setisShowLoader(true);

        RDApplications.RDApplicationsModel.getDepositDetailsByAcccount(account_number).then(async (res) => {
            setisShowLoader(false);
            if (res.statusCode == 200) {
                setEntries(res.body.message);
            } else {
                setEntries([]);
            }
        })
        setShowEntryModel(true);
    }
    const paidHandle = (account_number) => {
        // let dataArray = deposited_date.split('-');
        setPaidID({
            "account_number":account_number,
        });
        setShowDeleteModel(true);
    }

    const getMemberGroup = (pageno) => {
        setisShowLoader(true);
        let formatedAccounts = [];
        let payload = auth.id;
        if (auth.role == "admin" || auth.role == "maker" || auth.role == "checker") {
            payload = "all";
        }
        RDApplications.RDApplicationsModel.getAllRdApplications(payload).then(async (res) => {
            setisShowLoader(false);
            if (res.statusCode == 200) {
                let list = await User.Auth.getUsers();
                //setUserList(list);
                formatedAccounts = res.body.message.map(ac => {
                        let obj = list.body.message.filter(user => user.id == ac.agent_id)
                        ac["agent_name"] = obj[0]["name"];
                        return ac;
                });
                let result = formatedAccounts.filter(ac=>ac.is_account_closed==0);
                setRdAccounts(result);
                gotoPage(pageno);
            } else {
                setRdAccounts([]);
            }
        })
    }
    function DefaultColumnFilter({
        column: { filterValue, preFilteredRows, setFilter },
    }) {
        const count = preFilteredRows.length

        return (
            <input
                className="form-control"
                value={filterValue || ''}
                onChange={e => {
                    setFilter(e.target.value || undefined)
                }}
                placeholder={`Search ${count} records...`}
            />
        )
    }
    const defaultColumn = React.useMemo(
        () => ({
            // Default Filter UI
            Filter: DefaultColumnFilter,
        }),
        []
    )
    const paidEMI = async () => {
        try {
            if(amount !="" && amount !=0){
                setShowDeleteModel(false);
                setisShowLoader(true);
                let payload = {
                    account_number:paidID.account_number,
                    agent_id:auth.id,
                    deposited_amount:amount,
                    deposited_date:moment().format("YYYY-MM-DD"),
                    is_deposited:1,
                    is_account_open_amount:0,
                }
                let response = await rDApplications.RDApplicationsModel.postDeposit(payload);
                setisShowLoader(false);
                if (response.statusCode == 200) {
                    alert(response.body.message);
                    getMemberGroup(pageIndex);
                }
            }
        } catch (error) {
            console.log(error.response.body.message);
            setisShowLoader(false);
        }
    }

    const paginate = (totalPage) => {
        let pageNumberArray = [];
        for (let i = 1; i <= totalPage; i++) {
            pageNumberArray.push(i);
        }
        return (
            <Pagination>
                <Pagination.First onClick={() => gotoPage(0)} />
                <Pagination.Prev onClick={() => previousPage()} disabled={!canPreviousPage} />
                {pageNumberArray.map(num => <Pagination.Item key={num} active={num === (pageIndex + 1)} onClick={() => gotoPage(num - 1)}>{num}</Pagination.Item>)}
                <Pagination.Next onClick={() => nextPage()} disabled={!canNextPage} />
                <Pagination.Last onClick={() => gotoPage(totalPage - 1)} />
            </Pagination>
        );

    }
    const handleAmount =(e)=>{
        setAmount(e.target.value)
    }
    const deleteDeposit = async(data)=>{
        
        if (window.confirm(`Are you sure you want to delete Rs ${data.deposited_amount} from Account ${data.account_number} ?`)) {
            // Save it!
            setisShowLoader(true);
            let response = await rDApplications.RDApplicationsModel.deleteDeposit(data.id);
            setisShowLoader(false);
            if (response.statusCode == 200) {
                alert("Amount Deleted successfully.");
                let remainingEnitiy = entries.filter(ent=>ent.id!=data.id);
                setEntries(remainingEnitiy);
                getMemberGroup(pageIndex);
            }
      } else {
            // Do nothing!
            console.log('Thing was not saved to the database.');
          }
    }
    const handleInlineEdit=(id)=>{
            amountInputRefArr.forEach((entry,idx)=>{
                amountInputRefArr[idx].disabled =true;
                amountInputRefArr[idx].style.border = "0px";
                if(idx == id){
                    amountInputRefArr[idx].disabled =false;
                    amountInputRefArr[idx].style.border = "1px solid";
                    amountInputRefArr[idx].focus();
                    setSelectedEntryUpdate(idx);             
                }   
            })   
    }
    const handleInlineSave = async(id)=>{
        setShowEntryModel(false);
        setisShowLoader(true);
        let payload={
            "deposited_amount":amountInputRefArr[id].value,
            "id":id
        }
        let response = await rDApplications.RDApplicationsModel.updateDepositAmount(payload);
        setisShowLoader(false);
        if (response.statusCode == 200) {
            alert("Deposited Updated Successfully.");
            setSelectedEntryUpdate("");
            getMemberGroup(pageIndex);
            //amountInputRefArr[id].value = payload.deposited_amount;
        }

    }
    const handleInlineCancel = (id)=>{
        amountInputRefArr[id].disabled =true;
        amountInputRefArr[id].style.border = "0px";
        setSelectedEntryUpdate("");
    }
    const callbackRef=(el, id, amount)=>{
        if(el){
            amountInputRefArr[id] = el;
            amountInputRefArr[id].value = amount;
        }
    }
    const {
        getTableProps,
        getTableBodyProps,
        headerGroups,
        rows,
        prepareRow,
        pageOptions,
        page,
        pageCount,
        state: { pageIndex, pageSize },
        gotoPage,
        previousPage,
        nextPage,
        setPageSize,
        canPreviousPage,
        canNextPage,
    } = useTable({ columns, data, defaultColumn, initialState: { pageIndex: 0 } }, useFilters, useSortBy, usePagination)
    return (
        <>
            <Modal show={showDeleteModel} onHide={() => setShowDeleteModel(false)}>
                <Modal.Header closeButton>
                    <Modal.Title>Deposit Money </Modal.Title>
                </Modal.Header>
                    <Modal.Body>                        
                        <Form.Group as={Col} controlId="formGriddob" className="form-group required">
                            <Form.Label>Amount</Form.Label>
                            <Form.Control
                                name="amount"
                                type="number"
                                onChange={handleAmount}
                                value={amount}
                            />
                            {/* {formik.touched.account_holder_name && formik.errors.account_holder_name ? (
                                <div class="text-danger">{formik.errors.account_holder_name}</div>
                            ) : null} */}
                        </Form.Group>
                    </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={() => setShowDeleteModel(false)}>
                        Cancel
                    </Button>
                    <Button variant="primary" onClick={() => paidEMI()}>
                        Deposit
                    </Button>
                </Modal.Footer>
            </Modal>

            <Modal size="lg" show={showEntryModel} onHide={() => {setShowEntryModel(false);}}>
                <Modal.Header closeButton>
                    <Modal.Title>Deposites </Modal.Title>
                </Modal.Header>
                    <Modal.Body>
                        <Table className="shadow-lg p-3 mb-5 bg-white rounded small"  bordered responsive>
                            <thead className="bg-primary">
                                <tr>
                                    <th>
                                        Account Number
                                    </th>
                                    <th>
                                        Date
                                    </th>
                                    <th>
                                        Amount
                                    </th>
                                    <th>
                                        Action
                                    </th>
                                </tr>
                            </thead>
                            <tbody>
                                {entries.map(data=>(
                                    <tr>
                                        <td>
                                            {data.account_number}
                                        </td>
                                        <td>
                                            {moment(data.deposited_date).format("DD-MM-YYYY")}
                                        </td>
                                        <td>
                                            <input ref={(el) =>callbackRef(el,data.id,data.deposited_amount) } id={`deposit_${data.id}`} style={{"width":"50%" ,"border":"0px","background": "transparent"}} type="number" disabled={true}/>
                                        </td>
                                        <td>{selectedEntryUpdate==data.id?(<>
                                            <Button size={"sm"} className="mr-2" variant="warning" onClick={()=>handleInlineSave(data.id)} >Save</Button>
                                            <Button size={"sm"} variant="info" onClick={()=>handleInlineCancel(data.id)}>Cancel</Button></>)
                                            :
                                            (<><Button size={"sm"} className="mr-2" variant="success" onClick={()=>handleInlineEdit(data.id)} >Edit</Button>
                                            <Button size={"sm"} variant="danger" onClick={()=>deleteDeposit(data)}>Delete</Button></>)}
                                        </td>

                                    </tr>
                                ))}
                            </tbody>
                        </Table>                      
                    </Modal.Body>
            </Modal>


            <Loader show={isShowLoader} />
            <Container fluid>
                <h2 className="text-info text-center">RD applications</h2>
                <Row className="mb-5 mt-3">
                    <Col>
                        <Link to="/openNewRdAccount">
                            <Button variant="info" active>
                                Open new RD
                            </Button>
                        </Link>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <div className="text-right">
                            {paginate(pageCount)}
                        </div>

                        <Table {...getTableProps()} className="shadow-lg p-3 mb-5 bg-white rounded small" striped bordered hover responsive>
                            <thead className="bg-primary">
                                {headerGroups.map(headerGroup => (
                                    <tr {...headerGroup.getHeaderGroupProps()}>
                                        {headerGroup.headers.map(column => (
                                            <th
                                                {...column.getHeaderProps(column.getSortByToggleProps())}
                                            >
                                                {column.render('Header')}
                                                <span>
                                                    {column.isSorted ? (column.isSortedDesc ? ' ' : ' ') : ''}
                                                </span>
                                                <div>{column.allowFilter ? column.render('Filter') : null}</div>
                                            </th>
                                        ))}
                                    </tr>
                                ))}
                            </thead>
                            <tbody{...getTableBodyProps()}>

                                {page.map(row => {
                                    prepareRow(row)
                                    return (
                                        <tr {...row.getRowProps()}>
                                            {row.cells.map(cell => {
                                                console.log(cell);
                                                // if(cell.column.id !="col5"){                                
                                                return (
                                                    <td {...cell.getCellProps()}>
                                                        {cell.render('Cell')}
                                                    </td>
                                                )
                                            })}
                                        </tr>
                                    )
                                })}

                            </tbody>
                        </Table>
                        <div className="text-right">
                            {paginate(pageCount)}
                        </div>
                    </Col>
                </Row>
            </Container>
        </>
    );
}

export default RdDeposites;